import Vue from 'vue';
import App from './App.vue';
import vuetify from './plugins/vuetify';

Vue.config.productionTip = false;

new Vue({
  vuetify,
  render: (h) => h(App),
}).$mount('#app');

Vue.filter('capitalize', (value) => {
  if (!value) {
    return '';
  }
  const valueStr = value.toString();
  return valueStr.charAt(0).toUpperCase() + value.slice(1);
});
